.contact-us-title {
  color: $color-white;
  text-align: center;
  font-size: 32px;
  line-height: 32px;
  letter-spacing: -1.5px;
  width: 100%;

  @include onTablet {
      font-size: 48px;
      line-height: 48px;
      letter-spacing: -1.5px;
  }

  @include onDesktop {
    font-size: 64px;
    line-height: 64px;
    letter-spacing: -1.5px;
  }
}
